@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);
.app-football-feed .col:nth-child(even) .card{
  flex-direction: row-reverse;
}

.app-logo{position:relative;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.app-logo .logo-row{position:relative;display:flex;justify-content:center;align-items:center}.app-logo .logo-row span{position:relative;font-size:2rem;width:2rem;letter-spacing:10px;text-align:center;font-weight:bold}.app-logo .logo-row span:nth-child(odd){z-index:2}.app-logo .logo-row span:nth-child(even){z-index:0}.app-logo .logo-row .strike{width:100%;height:.5rem;position:absolute;margin:0 auto;transform:translate(-5px);z-index:1;background:#f1f1f1d8;border-radius:9999999px}@media only screen and (max-width: 500px){.app-logo .logo-row span{font-size:1.5rem;width:1.5rem;letter-spacing:5px}.app-logo .logo-row .strike{height:.3rem}}@media only screen and (max-width: 350px){.app-logo .logo-row span{font-size:1rem;width:1rem;letter-spacing:2px}.app-logo .logo-row .strike{height:.2rem}}

.leaflet-container{width:100%;height:100%;min-width:50px;min-height:250px}.leaflet-control-container{display:none}.app-simple-map.popup{padding:10px 0}

.app-meteo{width:100%;height:100%;background:linear-gradient(45deg, #1068b6, #ec60a6);display:grid;grid-template:50% 25% 25%/repeat(8, calc(100% / 8));position:relative;border:1px solid #f1f1f1;color:#f1f1f1}.bloc-logo-info{grid-area:1 / 1 / 2 / -1;display:flex;justify-content:center;align-items:center}.bloc-logo{width:50%}.bloc-logo img{width:100%;height:auto}.bloc-info{width:50%}.bloc-info .temps{font-size:1.25rem}.bloc-info .temps::first-letter{text-transform:capitalize}.bloc-info .temperature{font-size:1.5rem}.bloc-info .localisation{font-size:1.25rem;word-wrap:break-word}.heure-bloc-prevision{grid-area:2 / 1 / 3 / -1}.jour-prevision-bloc{grid-area:3 / 1 / 4 / -1}.jour-prevision-bloc,.heure-bloc-prevision{display:grid;grid-template:100%/repeat(7, 1fr)}.bloc-h,.bloc-j{display:flex;justify-content:center;align-items:center;flex-direction:column;border-top:1px solid #f1f1f1;font-size:1.2rem}.bloc-h:not(:last-child),.bloc-j:not(:last-child){border-right:1px solid #f1f1f1}.bloc-h p,.bloc-j p{margin:2px}.overlay-icone-chargement{position:absolute;width:100%;height:100%;background:linear-gradient(45deg, #3b3232, #161c1d);transition:opacity 1.1s ease-out;z-index:1}.overlay-icone-chargement img{width:150px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}

.carousel{height:240px !important}


*,::after,::before{margin:0;padding:0;box-sizing:border-box;font-family:'Open Sans', 'Avenir', Helvetica, Arial, sans-serif}

